@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800|Raleway:200,300,400,500,600i,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Basic Layout */
.app-wrapper {
  height: 100vh;
}
.psvn-main-view {
  position: relative;
  left: 280px;
  width: calc(100% - 280px);
  min-height: 100%;
  background-color: #f9f9f9;
}
.psvn-sidebar-wrapper {
  position: fixed;
  left: 0;
  width: 280px;
  height: 100%;
  background-color: #fff;
}
.psvn-header-wrapper {
  position: relative;
  width: 100%;
  height: 62px;
  background-color: #fff;
}
/* Default Aside CSS */
.psvn-sidebar-logo-wrapper .psvn-sidebar-logo {
  padding: 1.6rem;
  margin-bottom: 3.5rem;
  color: var(--primary-theme-color);
}
.psvn-sidebar-list {
  list-style: none;
}
.psvn-sidebar-list li {
  padding: 10px 15px;
  margin: 10px;
}
.psvn-sidebar-list li a {
  color: #707070;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.6rem;
}
.psvn-sidebar-list li a {
  display: flex;
}
.psvn-sidebar-list li:hover,
.psvn-sidebar-list li:focus {
  background: #f9f9f9;
  border-radius: 26px;
}
.psvn-sidebar-list li:hover a,
.psvn-sidebar-list li:hover .psvn-sidebar-list-item-icon img {
  color: var(--primary-theme-color);
}
.psvn-sidebar-list li:hover .psvn-sidebar-list-item-icon img.black {
  display: none;
}
.psvn-sidebar-list li:hover .psvn-sidebar-list-item-icon img.colored {
  display: inline-block;
}
.psvn-sidebar-list li .psvn-sidebar-list-item-icon img.black {
  display: inline-block;
}
.psvn-sidebar-list li .psvn-sidebar-list-item-icon img.colored {
  display: none;
}
.psvn-sidebar-list li .psvn-sidebar-list-item-icon img {
  margin-right: 10px;
  width: 22px;
}
.psvn-header-left-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  margin-left: 20px;
}
.psvn-header-menu-icon {
  cursor: pointer;
}
/* ============
  Fonts
===============*/

/*=============
General CSS 
===============*/
:root {
  --primary-font-family: "Open Sans", sans-serif;
  --secondary-font-family: "Raleway", sans-serif;
  --primary-theme-color: #f7390a;
}

html {
  font-size: 10px;
}
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  font-family: var(--primary-font-family);
  line-height: 1.5;
  background-color: #f6f6f6;
  min-height: 100vh;
}

/* PARAGRAPHS */
h1,
h2,
h3,
h4 {
  margin: 0;
  line-height: 1.25;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  font-family: var(--secondary-font-family);
}
h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.5em;
}
h3 {
  font-size: 1.2em;
}
h4 {
  font-size: 1em;
}
ul,
ol {
  margin: 1em 0;
  padding-left: 40px;
}

/* LINKS */
a {
  text-decoration: underline;
}
a:link {
  color: #0000ee;
}
a:visited {
  color: #551a8b;
}
a:active {
  color: #ee0000;
}

a:active,
a:hover {
  outline: none;
}

a img {
  border: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

/* FORMS */
input {
  margin: 0;
}
input:focus {
  outline: none;
}

.border-warning {
  border: 2px solid #d0021b !important;
}

.hidden {
  display: none;
}

.text-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.width-fit-content {
  width: -webkit-fill-available;
  width: -moz-available;
}

.multiline-truncate {
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  display: -moz-box;
  box-orient: vertical;
  word-break: break-word;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-between {
  display: flex;
  justify-content: space-between;
}

.ml-25 {
  margin-left: 1.5625rem;
}

.mr-25 {
  margin-right: 1.5625rem;
}

.inline-block {
  display: inline-block;
}

hr {
  border: 0;
  height: 2px;
  background: #e0e0e0;
}

.bottom-loader {
  display: flex;
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 17px;
}

